<template>
  <div>
      <div class="panelHeader" v-on:click="$parent.togglePanelVisibility('images')">
          <span class="panelTitle">Images</span>
      </div>
      <div v-show="$parent.visiblePanels.images"  class="panelBody">
          <div class="spacing"></div>
          <button v-on:click="showModalLoadImage()" class="btn btn-primary btn-sm">Load image</button>
          <!--
          <span>Load image:</span>
          <input type="file" id="inputImage" @change="$parent.onImageUpload" />
          -->
          <div class="spacing"></div>

          <table v-if="$parent.images.length > 0" class="smallTable contextPropertiesTable">
              <thead>
                  <tr>
                      <th>image</th>
                      <th>name</th>
                      <th>draw</th>
                      <th>draw scaled</th>
                  </tr>
              </thead>
              <tbody>
                  <tr v-for="image in $parent.images" v-bind:key="image.filename"> <!-- TODO: substituir v-bind:key "image.filename" por "image.id" -->
                      <td><img v-if="image.url" :src="image.url" style="max-width: 64px;"/></td>
                      <td>{{image.name}}</td>
                      <td><button v-on:click="selectImageToDraw(image)" class="btn btn-primary btn-sm">draw...</button></td>
                      <td><button v-on:click="selectImageToDrawScaled(image)" class="btn btn-primary btn-sm">draw scaled...</button></td>
                  </tr>
              </tbody>
          </table>
          <div class="spacing"></div>
      </div>

      <ModalLoadImage ref="modalLoadImage"></ModalLoadImage>
  </div>
</template>

<script>
//import IconUp from '@/components/icons/IconUp.vue'
//import IconDown from '@/components/icons/IconDown.vue'
import ModalLoadImage from '@/components/modals/ModalLoadImage.vue'

export default {
  name: 'ImagesPanel',
  components: {
    //IconUp, IconDown
    ModalLoadImage
  },
  props: {
  },
  data() {
    return {

    }
  },
  methods: {
    selectImageToDraw(image) {
      this.$parent.selectSampleScriptWithParams('Draw image');
      for (let i = 0; i < this.$parent.selectedSampleScriptWithParams.params.length; i++) {
        let param = this.$parent.selectedSampleScriptWithParams.params[i];
        if (param.name == "imageName") {
          param.value = image.name;
          //this.$parent.$forceUpdate();
        }
      }
    },
    selectImageToDrawScaled(image) {
      this.$parent.selectSampleScriptWithParams('Draw image scaled');
      for (let i = 0; i < this.$parent.selectedSampleScriptWithParams.params.length; i++) {
        let param = this.$parent.selectedSampleScriptWithParams.params[i];
        if (param.name == "imageName") {
          param.value = image.name;
          //this.$parent.$forceUpdate();
        }
      }
    },
    showModalLoadImage() {
       this.$refs.modalLoadImage.open();
       //window.$('#modalAddScriptParam').modal('show');
    },
    // showModalLoadImage() {
    //     window.$('#modalLoadImage').modal('show');
    // }
  }
}
</script>

