<template>
    <div class="modal fade" id="modalLoadImage" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="modalLabel">Load image</h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                <div>
                    <span>Load image: </span>
                    <input type="file" id="inputImage" @change="onImageUpload" />
                </div>
                <div v-if="image != null">
                    <img :src="image.url" style="max-width: 64px;"/>
                </div>
                <br>
                <div v-if="image != null">
                    <span>Name: </span>
                    <input v-model="image.name">
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-primary" v-if="image != null" v-on:click="loadImage()">OK</button>
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Cancel</button>
            </div>
            </div>
        </div>
    </div>
</template>

<script>
//import IconUp from '@/components/icons/IconUp.vue'
//import IconDown from '@/components/icons/IconDown.vue'

export default {
  name: 'ModalLoadImage',
  components: {
    //IconUp, IconDown
  },
  props: {
  },
  data() {
    return {
        image: null
    }
  },
  methods: {
    open() {
        this.image = null;
        window.$('#modalLoadImage').modal('show');
    },
    onImageUpload(e) {
        const file = e.target.files[0];
        let name = file.name.replace(/\.[^/.]+$/, "");
        let image = {
            "filename": file.name,
            "url": URL.createObjectURL(file),
            "name": name
        };
        this.image = image;
    },
    loadImage() {
        if (this.image != null) {
            this.$parent.$parent.images.push(this.image);
        }
        window.$('#modalLoadImage').modal('hide');
    }
  }
}
</script>

