let $util = {
    // createImageData(w,h) {
    //     let tmpCanvas = document.createElement('canvas');
    //     let tmpCtx = tmpCanvas.getContext('2d');
    //     return tmpCtx.createImageData(w,h);
    // },
    convoluteLayer(ctx, weights, opaque) {
        let width = ctx.canvas.width;
        let height = ctx.canvas.height;
        const imageData = ctx.getImageData(0, 0, width, height);

        let newImageData = this.convolute(imageData, weights, opaque);

        ctx.putImageData(newImageData, 0, 0);
    },
    convolute(pixels, weights, opaque) {
        var side = Math.round(Math.sqrt(weights.length));
        var halfSide = Math.floor(side/2);
        var src = pixels.data;
        var sw = pixels.width;
        var sh = pixels.height;
        // pad output by the convolution matrix
        var w = sw;
        var h = sh;

        //var output = Filters.createImageData(w, h);
        var output = document.createElement('canvas').getContext('2d').createImageData(w,h);

        var dst = output.data;
        // go through the destination image pixels
        var alphaFac = opaque ? 1 : 0;
        for (var y=0; y<h; y++) {
            for (var x=0; x<w; x++) {
                var sy = y;
                var sx = x;
                var dstOff = (y*w+x)*4;
                // calculate the weighed sum of the source image pixels that
                // fall under the convolution matrix
                var r=0, g=0, b=0, a=0;
                for (var cy=0; cy<side; cy++) {
                    for (var cx=0; cx<side; cx++) {
                        var scy = sy + cy - halfSide;
                        var scx = sx + cx - halfSide;
                        if (scy >= 0 && scy < sh && scx >= 0 && scx < sw) {
                            var srcOff = (scy*sw+scx)*4;
                            var wt = weights[cy*side+cx];
                            r += src[srcOff] * wt;
                            g += src[srcOff+1] * wt;
                            b += src[srcOff+2] * wt;
                            a += src[srcOff+3] * wt;
                        }
                    }
                }
                dst[dstOff] = r;
                dst[dstOff+1] = g;
                dst[dstOff+2] = b;
                dst[dstOff+3] = a + alphaFac*(255-a);
            }
        }
        return output;
    },
    rgbToHex(rgb) {
        return '#' + [rgb.r, rgb.g, rgb.b].map(x => {
            const hex = x.toString(16);
            return hex.length === 1 ? '0' + hex : hex;
          }).join('');
    },
    hexToRgb(hex) {
        let rgbArray = hex.replace(/^#?([a-f\d])([a-f\d])([a-f\d])$/i
             ,(m, r, g, b) => '#' + r + r + g + g + b + b)
            .substring(1).match(/.{2}/g)
            .map(x => parseInt(x, 16));
        return {r:rgbArray[0], g:rgbArray[1], b:rgbArray[2]};
    },
    rgbAdd(rgb, add) {
        rgb.r += add;
        rgb.g += add;
        rgb.b += add;

        rgb.r = this.rgbFit(rgb.r);
        rgb.g = this.rgbFit(rgb.g);
        rgb.b = this.rgbFit(rgb.b);

        return rgb;
    },
    rgbFit(num) {
        num = Math.round(num);
        return  Math.max(Math.min(num, 255), 0);
    },
    random256() {
        return Math.floor(Math.random()*256);
    },
    cloneCanvas(canvas) {
        let canvasClone = document.createElement('canvas');
        canvasClone.width = 600;
        canvasClone.height = 400;
        let ctxClone = canvasClone.getContext('2d');
        ctxClone.drawImage(canvas, 0, 0);
        return canvasClone;
    },
    getImageDataPosition(canvas, x, y) {
        let width = canvas.width;
        //let height = canvas.height;
        let position = (y * width + x) * 4;
        return position;
    },
    cropAt(originalCanvas, initialCropX, initialCropY, finalCropX, finalCropY) {
        let width = originalCanvas.width;
        let height = originalCanvas.height;

        let originalCtx = originalCanvas.getContext('2d');
        let imageData = originalCtx.getImageData(0, 0, width, height);

        let croppedWidth = finalCropX - initialCropX + 1;
        let croppedHeight = finalCropY - initialCropY + 1;

        let croppedcanvas = document.createElement('canvas');
        croppedcanvas.width = croppedWidth;
        croppedcanvas.height = croppedHeight;

        let croppedcanvasCtx = croppedcanvas.getContext('2d');
        croppedcanvasCtx.putImageData(imageData, -initialCropX, -initialCropY);

        return croppedcanvas;
    },
    crop(originalCanvas) {
        let width = originalCanvas.width;
        let height = originalCanvas.height;

        let originalCtx = originalCanvas.getContext('2d');
        let imageData = originalCtx.getImageData(0, 0, width, height);

        let initialCropX = 0;
        let initialCropY = 0;

        let finalCropX = width-1;
        let finalCropY = height-1;

        for (let x=0; x<width; x++) {
            let transparentCol = true;
            initialCropX = x;
            for (let y=0; y<height; y++) {
                let position = this.getImageDataPosition(originalCanvas, x, y);
                if (imageData.data[position+3] > 0) { //not transparent
                    transparentCol = false;
                    break;
                }
            }
            if (!transparentCol) {
                break;
            }
        }

        for (let x=width-1; x>=0; x--) {
            let transparentCol = true;
            finalCropX = x;
            for (let y=0; y<height; y++) {
                let position = this.getImageDataPosition(originalCanvas, x, y);
                if (imageData.data[position+3] > 0) { //not transparent
                    transparentCol = false;
                    break;
                }
            }
            if (!transparentCol) {
                break;
            }
        }

        for (let y=0; y<height; y++) {
            let transparentRow = true;
            initialCropY = y;
            for (let x=0; x<width; x++) {
                let position = this.getImageDataPosition(originalCanvas, x, y);
                if (imageData.data[position+3] > 0) { //not transparent
                    transparentRow = false;
                    break;
                }
            }
            if (!transparentRow) {
                break;
            }
        }

        for (let y=height-1; y>=0; y--) {
            let transparentRow = true;
            finalCropY = y;
            for (let x=0; x<width; x++) {
                let position = this.getImageDataPosition(originalCanvas, x, y);
                if (imageData.data[position+3] > 0) { //not transparent
                    transparentRow = false;
                    break;
                }
            }
            if (!transparentRow) {
                break;
            }
        }

        return this.cropAt(originalCanvas, initialCropX, initialCropY, finalCropX, finalCropY);

    },


    getColorAtPixel(imageData, x, y) {
        const {width, data} = imageData
        
        return {
            r: data[4 * (width * y + x) + 0],
            g: data[4 * (width * y + x) + 1],
            b: data[4 * (width * y + x) + 2],
            a: data[4 * (width * y + x) + 3]
        }
    },

    setColorAtPixel(imageData, color, x, y) {
        const {width, data} = imageData;
        
        data[4 * (width * y + x) + 0] = color.r & 0xff;
        data[4 * (width * y + x) + 1] = color.g & 0xff;
        data[4 * (width * y + x) + 2] = color.b & 0xff;
        data[4 * (width * y + x) + 3] = color.a & 0xff;
    },

    colorMatch(a, b) {
        return a.r === b.r && a.g === b.g && a.b === b.b && a.a === b.a;
    },

    floodFill(ctx, newColor, x, y) {
        let width = ctx.canvas.width;
        let height = ctx.canvas.height;
        let imageData = ctx.getImageData(0, 0, width, height);
        this.floodFillImageData(imageData, newColor, x, y);
        ctx.putImageData(imageData, 0, 0);
    },

    floodFillImageData(imageData, newColor, x, y) {
        //const {width, height, data} = imageData
        const {width, height} = imageData;
        const stack = [];
        const baseColor = this.getColorAtPixel(imageData, x, y);
        let operator = {x, y};
      
        // Check if base color and new color are the same
        if (this.colorMatch(baseColor, newColor)) {
          return;
        }
      
        // Add the clicked location to stack
        stack.push({x: operator.x, y: operator.y})
      
        while (stack.length) {
          operator = stack.pop()
          let contiguousDown = true // Vertical is assumed to be true
          let contiguousUp = true // Vertical is assumed to be true
          let contiguousLeft = false
          let contiguousRight = false
      
          // Move to top most contiguousDown pixel
          while (contiguousUp && operator.y >= 0) {
            operator.y--
            contiguousUp = this.colorMatch(this.getColorAtPixel(imageData, operator.x, operator.y), baseColor)
          }
      
          // Move downward
          while (contiguousDown && operator.y < height) {
            this.setColorAtPixel(imageData, newColor, operator.x, operator.y)
      
            // Check left
            if (operator.x - 1 >= 0 && this.colorMatch(this.getColorAtPixel(imageData, operator.x - 1, operator.y), baseColor)) {
              if (!contiguousLeft) {
                contiguousLeft = true
                stack.push({x: operator.x - 1, y: operator.y})
              }
            } else {
              contiguousLeft = false
            }
      
            // Check right
            if (operator.x + 1 < width && this.colorMatch(this.getColorAtPixel(imageData, operator.x + 1, operator.y), baseColor)) {
              if (!contiguousRight) {
                stack.push({x: operator.x + 1, y: operator.y})
                contiguousRight = true
              }
            } else {
              contiguousRight = false
            }
      
            operator.y++
            contiguousDown = this.colorMatch(this.getColorAtPixel(imageData, operator.x, operator.y), baseColor)
          }
        }
      }



}

export default $util
